<template>
  <div>
    <v-row class="subtitle-2 text--primary mb-2 mt-4">
      <v-col cols="12" md="3">
        <v-autocomplete
          autocomplete="off"
          outlined
          dense
          v-model="record.country"
          :items="countries"
          label="Country"
          item-text="name"
          item-value="_id"
          @change="getRegions(record.country)"
        ></v-autocomplete>
      </v-col>

      <v-col cols="12" md="3">
        <v-autocomplete
          autocomplete="off"
          outlined
          dense
          v-model="record.zone"
          :items="zones"
          label="Zone"
          item-text="name"
          item-value="_id"
        ></v-autocomplete>
      </v-col>

      <v-col cols="12" md="3">
        <v-text-field
          label="Farm Size (Acres/Hectors)"
          outlined
          dense
          v-model="record.acres"
          type="number"
        ></v-text-field>
      </v-col>

      <v-col cols="12" md="3">
        <v-autocomplete
          autocomplete="off"
          outlined
          dense
          v-model="record.status"
          :items="farmOptions"
          label="Status"
          item-text="name"
          item-value="value"
        ></v-autocomplete>
      </v-col>
    </v-row>

    <v-divider></v-divider>

    <v-row class="mt-2">
      <v-col cols="12" md="3">
        <v-text-field label="Location" outlined dense v-model="record.location"></v-text-field>
      </v-col>

      <v-col cols="12" md="3">
        <v-text-field label="Sub Location" outlined dense v-model="record.subLocation"></v-text-field>
      </v-col>

      <v-col cols="12" md="3">
        <v-text-field label="Soil Test" outlined dense v-model="record.soilTest"></v-text-field>
      </v-col>

      <v-col cols="12" md="3">
        <v-text-field label="Rainfall" outlined dense v-model="record.rainfall"></v-text-field>
      </v-col>

      <v-col cols="12" md="3">
        <v-text-field label="Minimum Tenure" outlined dense v-model="record.minimumTenure"></v-text-field>
      </v-col>

      <v-col cols="12" md="3">
        <v-text-field label="Maximum Tenure" outlined dense v-model="record.maximumTenure"></v-text-field>
      </v-col>

      <v-col cols="12" md="3">
        <v-text-field label="Electricity" outlined dense v-model="record.electricity"></v-text-field>
      </v-col>

      <v-col cols="12" md="3">
        <v-text-field label="Domestic water" outlined dense v-model="record.domesticWater"></v-text-field>
      </v-col>

      <v-col cols="12" md="3">
        <v-text-field label="Water for irrigation" outlined dense v-model="record.irrigationWater"></v-text-field>
      </v-col>

      <v-col cols="12" md="3">
        <v-text-field label="Distance from main road" outlined dense v-model="record.distance"></v-text-field>
      </v-col>

      <v-col cols="12" md="3">
        <v-text-field label="Type of land ownership" outlined dense v-model="record.ownership"></v-text-field>
      </v-col>

      <v-col cols="12" md="3">
        <v-text-field label="Suggested crops" outlined dense v-model="record.crops"></v-text-field>
      </v-col>

      <v-col cols="12" md="3">
        <v-text-field label="Market price per acre" outlined dense v-model="record.marketPrice"></v-text-field>
      </v-col>

      <v-col cols="12" md="3">
        <v-text-field label="Price Negotiation" outlined dense v-model="record.priceNegotiation"></v-text-field>
      </v-col>

      <v-col cols="12" md="3">
        <v-text-field label="Accessibility of labor" outlined dense v-model="record.labor"></v-text-field>
      </v-col>

      <v-col cols="12" md="3">
        <v-text-field
          label="Accessibility of agri machinery"
          outlined
          dense
          v-model="record.machinery"
        ></v-text-field>
      </v-col>

      <v-col cols="12" md="3">
        <v-text-field label="Video clip link" outlined dense v-model="record.videoClip"></v-text-field>
      </v-col>

      <v-col cols="12" md="3">
        <v-text-field label="Processing fee / acre" outlined dense v-model="record.processingFee"></v-text-field>
      </v-col>
    </v-row>

    <v-row class="mt-2">
      <v-col cols="12" md="12">
        <v-btn v-if="createStatus == true" color="accent" :loader="loader" @click="sendData">
          <v-icon left>mdi-content-save</v-icon>Save
        </v-btn>

        <v-btn v-else color="accent" :loader="loader" @click="sendData">
          <v-icon left>mdi-content-save</v-icon> Update
        </v-btn>
      </v-col>
    </v-row>

    <snackbar ref="snackbar"></snackbar>   
  </div>
</template>

<script>
export default {
  props: {
    docCreate: {
        type: Boolean
    },
    initial: {
      type: Object
    }
  },
  data() {
    return {
      record: {},
      createStatus: true,
      loader: false,
      countries: [],
      zones: [],
      farmOptions: [
        { name: "Partnership", value: "Partnership" },
        { name: "For Lease", value: "For Lease" },
        { name: "For Sale", value: "For Sale" }
      ],
    };
  },
  watch: {
    initial: {
        handler: "setInitial",
        immediate: true
    },
    docCreate: {
        handler: "setStatus",
        immediate: true
    }
  },
  methods: {
    setInitial(val) {
        this.record = { ...val };
        if(val.country) {
            this.getRegions(val.country._id);
        }
    },
    setStatus(val) {
        this.createStatus = val;
    },
    sendData() {
        if(this.record.country == undefined || this.record.zone == undefined) {
            this.$refs.snackbar.show("Please select country and zone!", "red");
        } else {
        const details = this.record;
        this.loader = true;
        this.$emit("data", details);
        }
    },
    getCountries() {
      const self = this;
      this.$store
        .dispatch("get", `/countries`)
        .then(res => {
          if (res.state) {
            self.countries = res.records;
          } else {
            this.$refs.snackbar.show(res.msg, "red");
          }
        })
        .catch(err => {
          this.$refs.snackbar.show(err, "red");
        });
    },
    getRegions(id) {
      const self = this;
      this.$store
        .dispatch("get", `/regions/by/country/${id}`)
        .then(res => {
          if (res.state) {
            self.zones = res.records;
          } else {
            this.$refs.snackbar.show(res.msg, "red");
          }
        })
        .catch(err => {
          this.$refs.snackbar.show(err, "red");
        });
    },
  },
  created () {
    this.getCountries();
  }
};
</script>

